body{
  scroll-behavior:smooth;
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  /* border: 1px solid; */
  font-size: 17px;
  position: relative;
}
.header > .logo-div{
  width: 150px;
}
.logo-div > img{
  width: 100%;
}
.nav-list{
  width: 40%;
}
.nav-list > ul{
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  text-decoration: none;

}
.nav-link{
  text-decoration: none;
  color: rgb(65,68,65);
  font-weight: bold;
}

.sign-register-btn{
  /* border: 1px solid; */
  width: 20%;
  display: flex;
  justify-content: space-between;
}

.register{
  width: 120px;
  padding: 15px 10px;
  font-size: 17px;
  font-weight: bold;
  background: rgb(255,77,48);
  color: aliceblue;
  border: none;
  border-radius: 2px;
}
.signup{
  width: 120px;
  padding: 15px 10px;
  font-size: 17px;
  font-weight: bold;
  border: none;
  background: none;
}

.responsive-bar{
  display: none;
}
.bar-icons{
  display: flex;
  width: 100%;
}
.bar-icons > span {
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.bar-icons > span > img{
  width: 75%;
}

.slide-transition{
  display: none;
  height: 100vh;
  background: white;
  position: fixed;
  top: 0;
  right: 0;
  overflow: auto;

  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(8px);
  z-index: 1;
}



.slide-transition > .top-r-bar {
  width: 100%;
  margin: 10px 0px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.336);
}
.slide-transition > .top-r-bar > span{
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  cursor: pointer;
}
 .top-r-bar > span > img{
  width: 75%;
}

.bottom-r-bar {
  width: 100%;
  /* border: 1px solid; */
}
.bottom-r-bar > ul{
  list-style-type: none;
  padding: 0px;
}
.bottom-r-bar > ul > li{
  margin: 10px;
  padding: 10px;
  cursor: pointer;
} 





.body{
  width: 100%;
  /* border: 1px solid; */
}

.container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  /* height: 85vh; */
}
.left-box{
  width: 40%;
}
.left-box > h3{
  font-size: 24px;
  color: rgb(9,35,55);
}
.left-box > h1{
font-size:50px;
font-weight: 900;
color: rgb(1,1,3);
line-height: 55px;

}
.left-box > h5{
  color: rgb(122,132,153);
  font-size: 17px;
}
.bottom-left{
  width: 70%;
  display: flex;
  justify-content:space-between;
}
.bottom-left > .book{
  padding: 15px 10px;
  font-size: 17px;
  width: 160px;
  font-weight: bold;
  border-radius: 2px;
  background: rgb(255,77,48);
  color: white;
  border: 2px solid transparent;

}
.learn{
  padding: 15px 10px;
  font-size: 17px;
  width: 160px;
  font-weight: bold;
  border-radius: 2px;
  background: rgb(1,1,3);
  color: white;
  border: 2px solid transparent;
}
.learn:hover{
  border: 2px solid black;
  color: black;
  background: none;
  transition: all .35s;
}

.right-box{
  width: 59%;
}
.right-box > img{
  width: 100%;
}


.booking-container{
  padding: 70px 20px;
  width: 93%;
  /* border: 1px solid; */
  border-radius: 5px;

  margin: 20px auto;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.input-container{
  width: 95%;
  display: flex;
  justify-content: space-between;
}
.input-container > div{
  width: 30%;
  display: flex;
  flex-direction: column;
}
.input-container > div > label{
  font-size: 16px;
  color:rgb(1,1,3) ;
  font-weight: bold;
  margin: 10px;
}
.input-container > div > select{
  padding: 10px;
  border-color: rgb(217,225,236);
  outline: none;
  color: rgb(86, 90, 103);

}
.input-container > div > input{
  padding: 10px;
  outline: none;
  border: 1px solid rgb(217,225,236);
}

.search-car{
  padding: 10px 15px;
  font-size: 17px;
  font-weight: bold;
  color: white;
  background: rgb(255,77,48);
  border: none;
}

.trip-container{
  padding: 70px 20px;
  text-align: center;
}
.trip-container > .trip-top > h1{
  font-size: 45px;
}
.trip-container > .trip-top > h2{
  font-size: 23px;
}
.trip-bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  /* border: 1px solid; */
  margin: 20px auto;
}
.trip-box{
  width: 30%;
  /* border: 1px solid; */
}

 .trip-box > h3{
  font-size: 23px;

 }
 .trip-box > p{
  color: rgb(112,111,123);
 }
 .trip-box > .trip-img{
  width: 100%;
 }
 .trip-img > img{
    width: 60%;
 }

 .model-container{
  
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 30px;
  /* border: 1px solid; */
 }
 .model-heading{
  width: 45%;
  margin: 0px auto;
 }
 .model-heading > h3{
  font-size: 23px;
 }
 .model-heading > h1{
  font-size: 45px;
 }
 .model-heading p{
  font-size: 17px;
 }

 .model-types{
  margin-top: 40px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border: 1px solid; */
 }
 .model-list{
  display: flex;
  flex-direction: column;
  width: 20%;
 }
 .model-list > button{
  font-size: 18px;
  text-align: left;
  margin: 2px 0px;
  font-weight: bold;
  padding: 20px 15px;
  background: rgb(233,233,233);
  border: none;
 }
 .model-list > button:hover{
  background: rgb(255,77,48);
  color: #fff;
  transition: all .5s;
 }
 .model-img{
  width: 50%;
  padding: 20px;
 }
 .model-img > img{
  width: 80%;
 }


 .model-detail{
  width: 22%;
  /* border: 1px solid; */
  padding: 0px;
 }
 .model-detail > .table{
  padding: 0px;
  width: 100%;
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
 }
 .table > .price{
  font-size: 17px;
  background:rgb(255,77,48) ;
  color: #fff;
 }
.table-div{
  border-left: 2px solid rgb(148,147,156);
  border-right: 2px solid rgb(148,147,156);
  border-bottom:2px solid rgb(148,147,156) ;
}
 .model-name{
  display: flex;
  justify-content: space-between;
  align-items: center;
 }
 .model-name > span{
  width: 50%;
  text-align: center;
  font-size: 17px;
  padding: 10px;
 }


 .promo-container{
  width: 100%;
  margin-top: 80px;
 }
 .promo-top{
  display: flex;
  flex-direction: column;
  background: rgb(45,45,45);
  align-items: center;
  padding: 20px;
  color: #fff;
 }
 .promo-top > h1{
  font-size: 45px;
  font-weight: bold;
 }
 .promo-top > h3{
  font-size: 28px;
  font-weight: 400;
 }

 .promo-middle{
  padding: 40px 0px;
  width: 100%;
  /* border: 1px solid red; */
  text-align: center;
 }
 .promo-middle > img{
  width: 90%;
 }


 .promo-bottom{
  width: 100%;
  margin: 30px 0px;
  display: flex;
  align-items:flex-start;
  justify-content: space-evenly;
 }
 .promo-bottom > .promo-bottom-left{
  width: 45%;
  /* border: 1px solid; */
 }
 .promo-bottom-left > h1{
  font-size: 45px;
  font-weight: bold;
 }
 .promo-bottom-left > h3{
  font-size: 25px;
 }
 .promo-bottom-left > p{
  font-size: 17px;
  color: rgb(112,111,123);
 }
 .promo-bottom-left > button{
  padding: 15px 20px;
  font-size: 20px;
  border-radius: 2px;
  border: none;
  font-weight: bold;
  background:rgb(255,77,48) ;
  color: #fff;
 }


.promo-bottom > .promo-bottom-right{
  width: 45%;
  /* border: 1px solid; */
 }


 .bottom-right-box{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
 }
 .bottom-right-box > span{
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
 }
 .bottom-right-box > span > img{
  width: 85%;
 }

 .bottom-right-box > .info{
  width: 65%;
 }
 .info > h2{
  font-size: 25px;
  font-weight: 800;
 }
 .info > p{
  font-size: 17px;
  color: rgb(112,111,123);
 }

 .faq-container{
  width: 100%;
  margin: 30px 0px;
  background: rgb(246,246,246);
  padding: 20px 0px;
 }
 .faq-top{
  width: 55%;
  margin: 0px auto;
  text-align: center;
 }
 .faq-top > h1{
  font-size: 45px;
 }
 .faq-top > p{
  font-size: 17px;
  color: rgb(112,111,123);
 }
 .faq-bottom{
  width: 55%;
  margin: 50px auto;
  border-radius: 3px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
 }
 .faq-box{
  padding:10px 30px;
  /* border: 1px solid; */
  font-size: 20px;
  font-weight: 500;

 }
 .question{
  display: flex;
  justify-content: space-between;
  align-items: center;
 }
 .arrow_btn{
  width:40px;
  height:30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  cursor: pointer;
 }
 .arrow-img{
  width:80%;
 }


 .answer{
  font-size: 17px;
  color: rgb(112,111,123);
  margin: 0px 0px;
 }
 
 


 .footer{
  width: 100%;
  margin-top:40px;
  display: flex;
  justify-content: space-between;
  padding-bottom: 50px;
  /* align-items: center; */
 }
 .footer-box{
  width: 27%;
  padding: 10px;
 }
 .footer-box > ul{
  list-style-type: none;
 }
 .footer-box > ul > li{
  margin: 10px 0px;
 }
 .footer-box > ul > li > a{
  text-decoration: none;
  color: black;
  padding: 0px 5px;
  font-weight: 500;
 }

 .footer-box > ul > .header-list{
  font-weight: 900;
  font-size: 23px;
 }
 .phone-info,.email-info{
  display: flex;
  /* justify-content: space-between; */
  align-items: left;
 }
 .phone-icon , .email-icon{
  width: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
 }
 .phone-icon > img{
  width: 100%;
 }
 .email-icon > img{
  width: 100%;
 }

 .get-in-touch{
  display: flex;
  flex-direction: column;
 }
 .get-in-touch > input{
  padding: 10px ;
  text-align: center;
  outline: none;
  font-size: 16px;
  background: rgb(236,236,236);
  border: none;
 }
 .get-in-touch > button{
  padding: 15px;
  font-size: 17px;
  color: #fff;
  background:rgb(255,77,48) ;
  font-weight: bold;
  border: none;
  border-radius: 3px;
  margin-top: 5px;
 }


